import axios from "../utils/request";
namespace Read {
  export interface goodsInfo {
    goodsId: string;
  }
  export interface updateProgress {
    fileId: string;
    readingLocation: string;
  }
  export interface filesId {
    filesId: string;
    type: string;
  }
  export interface noteId {
    noteId: string;
  }
  export interface addNote {
    goodsId: string;
    goodsFileId: string;
    originalText: string;
    textRange: string;
    href: string;
    noteContent: string;
  }
  export interface bookData {
    fileType: string;
    goodsFileId: string;
    goodsId: string;
    relevanceId: string;
    resource: string;
    resourceType: string;
    teachInstId: string;
  }
  export interface integral {
    id: number;
  }
}
// 获取商品信息
export const GetGoodsInfo = (params: Read.goodsInfo, baseURL: string) => {
  // 返回的数据格式可以和服务端约定
  return axios.get(
    baseURL + "/ouchn-customapi/device/goods/selGoodsById",
    params
  );
};

// 获取调用阅读时长接口频率
export const GetTimeParameter = (baseURL: string) => {
  // 返回的数据格式可以和服务端约定
  return axios.get(
    baseURL + "/ouchn-customapi/device/read/selectTimeParameter"
  );
};

// 更新用户阅读时长
export const UpdateOcDownloadReading = (
  params: Read.bookData,
  baseURL: string
) => {
  // 返回的数据格式可以和服务端约定
  return axios.post(
    baseURL + "/ouchn-customapi/device/read/updateOcDownloadReading",
    params
  );
};

// 更新用户阅读进度
export const UpdateOcReadSchedule = (
  params: Read.updateProgress,
  baseURL: string
) => {
  // 返回的数据格式可以和服务端约定
  return axios.post(
    baseURL + "/ouchn-customapi/device/read/updateOcReadSchedule",
    params
  );
};

// 获取用户添加的笔记
export const GetNoteInfo = (params: Read.filesId, baseURL: string) => {
  // 返回的数据格式可以和服务端约定
  return axios.get(
    baseURL + "/ouchn-customapi/device/api/note/getNoteInfo",
    params
  );
};

// 添加笔记
export const AddNote = (params: Read.addNote, baseURL: string) => {
  // 返回的数据格式可以和服务端约定
  return axios.post(
    baseURL + "/ouchn-customapi/device/api/note/addNote",
    params
  );
};

// 修改添加的笔记
export const UpdateNote = (params: Read.addNote, baseURL: string) => {
  // 返回的数据格式可以和服务端约定
  return axios.post(
    baseURL + "/ouchn-customapi/device/api/note/updateNote",
    params
  );
};

// 删除添加的笔记
export const DeleteNote = (params: Read.noteId, baseURL: string) => {
  // 返回的数据格式可以和服务端约定
  return axios.post(
    baseURL +
      "/ouchn-customapi/device/api/note/deleteNote?noteId=" +
      params.noteId
  );
};

