
import { defineComponent, ref, reactive, toRefs, onMounted, toRaw } from "vue";
import { useRouter } from "vue-router";
import { Toast } from "vant";
import {
  GetGoodsInfo,
  GetTimeParameter,
  UpdateOcDownloadReading,
  UpdateOcReadSchedule,
  GetNoteInfo,
  AddNote,
  UpdateNote,
  DeleteNote,
  GetInsertNumber,
  InsertIntegrals,
} from "../../api/read";
export default defineComponent({
  name: "Index",
  setup() {
    let data = reactive({
      notesList: [],
      bookmarkList: [],
      lineationList: [],
      sourceList: [],
      TimeParameter: 10,
      integralBox: false,
      integral: 15,
      width: 22,
    });
    // 获取路由参数
    const router = useRouter().currentRoute.value.query;
    if (router.token) {
      localStorage.setItem("Gk-Read-Token", router.token as any);
    }
    const baseURL = router.baseURL as any;
    const bookInfo = {
      bookPath:
        (router.bookPath as any) ||
        // "http://192.168.1.232:8080/9787521901214/container.xml",
        "https://ouchn-prod-public.oss-cn-beijing.aliyuncs.com/goods_file/beea6394-0553-4b53-8738-6ad2847f187f.epub",
      // "https://ouchn-prod-public.oss-cn-beijing.aliyuncs.com/goods_file/51c80e60-ac57-46d2-ad63-65e467a50f06.epub",
      // "https://ouchn-dev-public.oss-cn-qingdao.aliyuncs.com/goods_file/dae840c2-337c-4fc2-b6a3-eedc5dea085c.epub",
      // "https://ouchn-prod-public.oss-cn-beijing.aliyuncs.com/goods_file/fef8e6b9-503d-4aff-a199-c34c3e60fc13.epub",
      // "https://gateway.ysy.crtvup.com.cn/ouchn-source/goodsFile/downloadEncryptFile/1562769824491331586.epub?key=41729bb7-f633-4a57-9c51-f6f0f84d5589",
      // "https://gateway.ysy.crtvup.com.cn/ouchn-source/goodsFile/downloadEncryptFile/1562771181319315458.epub?key=f939f92a-64af-4b0f-b7cf-81e3f4e56c0d",
      // "/123.epub",
      // "/e123.epub",

      location: (router.location as any) || "0",
      fileType: (router.fileType as any) || "epub",
      goodsFileId: (router.goodsFileId as any) || "",
      goodsId: (router.goodsId as any) || "",
      relevanceId: (router.relevanceId as any) || "",
      resource: (router.resource as any) || "",
      resourceType: (router.resourceType as any) || "",
      teachInstId: (router.teachInstId as any) || "",
      limitSize: Number(router.limitSize as any) || 100, //限制阅读内容大小
      innerHeight: Number(router.innerHeight as any) || 500,
      innerWidth: Number(router.innerWidth as any) || 375,
      luminance: (router.luminance as any) || "luminance", //是否显示亮度
      isNote: (router.isNote as any) || "yes", //是否可以添加笔记
      systemType: (router.systemType as any) || "",
    };

    // 获取商品信息
    function getGoodsInfo() {
      GetGoodsInfo(
        {
          goodsId: bookInfo.goodsId,
        },
        baseURL
      ).then((res) => {
        data.sourceList = (res.data as any).sourceList || [];
      });
    }
    // 更新用户阅读时长
    function UpdateReadingTime() {
      UpdateOcDownloadReading(
        {
          fileType: bookInfo.fileType,
          goodsFileId: bookInfo.goodsFileId,
          goodsId: bookInfo.goodsId,
          relevanceId: bookInfo.relevanceId,
          resource: bookInfo.resource,
          resourceType: bookInfo.resourceType,
          teachInstId: bookInfo.teachInstId,
        },
        baseURL
      );
    }
    // 更新用户阅读进度
    function updateProgress() {
      UpdateOcReadSchedule(
        {
          fileId: bookInfo.goodsFileId,
          readingLocation:
            localStorage.getItem("location_" + bookInfo.goodsFileId) || "",
        },
        baseURL
      );
    }

    // 获取全部笔记
    function getNoteInfo(type: string, toast: string) {
      GetNoteInfo({ filesId: bookInfo.goodsFileId, type: type }, baseURL).then(
        (res) => {
          if (toast) {
            Toast(toast);
          }
          if (type == "note") {
            data.notesList = (res.data as any) || [];
          } else if (type == "bookmark") {
            data.bookmarkList = (res.data as any) || [];
          } else if (type == "lineation") {
            data.lineationList = (res.data as any) || [];
          }
        }
      );
    }

    // 确认添加笔记
    function confirmAddNotes(e: any) {
      addNoteBookmark(e, "note");
    }
    // 添加书签
    function addBookmark(e: any) {
      addNoteBookmark(e, "bookmark");
    }
    // 删除书签
    function deleteBookmark(e: any) {
      deleteNoteBookmark(e, "bookmark");
    }
    // 划线
    function addUnderline(e: any) {
      addNoteBookmark(e, "lineation");
    }

    // 添加笔记、书签、划线（笔记：note 书签：bookmark 划线：lineation）
    function addNoteBookmark(e: any, type: string) {
      const note = {
        goodsId: bookInfo.goodsId,
        goodsFileId: bookInfo.goodsFileId,
        originalText: e.originalText,
        textRange: e.textRange,
        href: e.href,
        noteContent: e.noteContent,
        type: type,
      };

      AddNote(note, baseURL).then((res) => {
        getNoteInfo(type, "添加成功");
        // Toast("添加成功");
      });
    }

    // 确认修改笔记
    function confirmAmendNotes(e: any) {
      UpdateNote(e, baseURL).then((res) => {
        Toast("修改成功");
      });
    }
    // 删除笔记
    function deleteNotes(e: any, type: string) {
      deleteNoteBookmark(e, type);
    }

    // 删除笔记、书签、划线
    function deleteNoteBookmark(e: any, type: string) {
      DeleteNote({ noteId: e.noteId }, baseURL).then((res) => {
        getNoteInfo(type, "删除成功");
        // Toast("删除成功");
      });
    }
    // 判断是否可以增加积分
    function isAddIntegral() {
      GetInsertNumber(
        {
          id: 6,
        },
        baseURL
      ).then((res) => {
        if (res.msg == "继续加") {
          data.integral = toRaw(res.data as any).quantity;
          data.integralBox = true;
          timeMeter();
        }
      });
    }
    function timeMeter() {
      // 15.5秒走一次，15分钟加 58px
      const timer = setInterval(() => {
        data.width += 1;
        if (data.width >= 80) {
          InsertIntegrals(
            {
              id: 6,
            },
            baseURL
          ).then((res) => {
            data.integralBox = false;
            data.width = 22;
            clearInterval(timer);
            // timer = null;
            if (res.data == true) {
              Toast("添加积分成功");
              isAddIntegral();
            } else {
              Toast("今天已经领完了，明天再试试吧！");
            }
          });
        }
      }, 15500);
    }

    onMounted(() => {
      if (bookInfo.goodsId) {
        isAddIntegral();
        getGoodsInfo();
        getNoteInfo("note", "");
        getNoteInfo("bookmark", "");
        getNoteInfo("lineation", "");
        GetTimeParameter(baseURL).then((res) => {
          data.TimeParameter = (res.data as any) || 1;
          setInterval(() => {
            UpdateReadingTime();
            updateProgress();
          }, data.TimeParameter * 60000);
        });
      }
    });
    return {
      data,
      bookInfo,
      ...toRefs(data),
      getNoteInfo,
      confirmAddNotes,
      confirmAmendNotes,
      deleteNotes,
      addBookmark,
      deleteBookmark,
      addUnderline,
    };
  },
});
